.footer {
  $this: &;

  position: relative;
  z-index: 3;

  padding-top: 10px;

  color: get-color(background);
  background: get-color(on-background);
  text-align: center;
  position: relative;
  font-size: font-size(14px);

  @include media-query(sm) {
    padding-top: 20px;
  }

  @include media-query(md) {
    text-align: left;
    font-size: font-size(16px);
  }

  &__inner {
    border-top: $divider-border;
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__nav {
    margin-top: 16px;

    @include media-query(md) {
      margin-top: 0;
    }
  }

  &__list {
    display: flex;

    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;

    @include media-query(md) {
      flex-direction: row;
    }

    &--socials {
      flex-direction: row;

      @include media-query(md) {
        justify-content: flex-end;
        margin-right: -16px;
      }
    }

    &--menu {
      @include media-query(md) {
        justify-content: flex-start;
        margin-left: -16px;
      }
    }

    &--bottom {
      @include media-query(md) {
        justify-content: flex-start;
      }
    }
  }

  &__list-item {
    flex-shrink: 0;
    position: relative;
    flex: 0 0 auto;

    &:hover {
      #{$this}__dropdown {
        display: block;
      }
    }
  }

  &__social-link {
    display: block;
    position: relative;
    padding: 8px 16px;
    color: get-color(footer-link);

    &:hover {
      color: get-color(footer-link-hover);
    }

    svg {
      width: 18px;
      height: 18px;
    }
  }

  &__social-badge {
    position: absolute;
    top: -10px;
    right: 8px;
    background-color: get-color(live-badge-background);
    color: get-color(live-badge-text);
    border-radius: 25px;
    animation: 5s blink ease infinite;
    transition-delay: 5s;
    padding: 0 6px;
    font-size: 10px;
    font-weight: bold;
    text-transform: uppercase;

    @keyframes blink {
      from, to {
        opacity: 0;
      }
      50% {
        opacity: 1;
      }
    }
  }

  &__menu-link {
    display: block;
    padding: 8px 16px;
    color: get-color(footer-link);
    text-decoration: none;

    &--icon {
      display: flex;
      line-height: 14px;
      align-items: center;

      &.active {
        #{$this}__link-icon {
          transform: rotate(180deg);
        }
      }
    }

    &:hover {
      color: get-color(footer-link-hover);
    }
  }

  &__bottom {
    margin-top: 16px;
    color: get-color(footer-text-pale);

    @include media-query(md) {
      margin-top: 0;
    }
  }

  &__copy {
    padding: 8px 0;

    @include media-query(md) {
      margin-right: 16px;
    }
  }

  &__powered-by {
    a {
      display: inline-block;
      padding: 8px 16px;
      color: inherit;
      text-decoration: none;

      &:hover {
        color: get-color(background);
      }
    }
  }

  &__link-icon {
    margin-left: 8px;

    svg {
      width: 12px;
      height: 12px;
    }
  }

  &__dropdown {
    display: none;

    justify-content: center;

    position: absolute;
    z-index: 1;
    left: 12px;
    bottom: 31px;

    margin: 0;
    border-radius: $input-border-radius;

    min-width: 120px;
    background: get-color(background);

    text-align: left;

    &--content {
      left: 7px;
      min-width: 70px;
      overflow: hidden;

      #{$this}__dropdown-inner {
        max-height: 200px;
        overflow: auto;

        &::-webkit-scrollbar {
          width: 4px;
          background-color: transparent;
        }

        &::-webkit-scrollbar-thumb {
          border-radius: var(--input-corner-radius);
          -webkit-box-shadow: inset 0 0 6px rgba(get-color(on-background), 0.3);
          background-color: get-color(on-background);
        }

        &::-webkit-scrollbar-track {
          -webkit-box-shadow: inset 0 0 6px rgba(get-color(on-background), 0.3);
          border-radius: var(--input-corner-radius);
          background-color: transparent;
        }

        &::-webkit-scrollbar-thumb:window-inactive {
          background-color: transparent;
        }
      }
    }
  }

  &__dropdown-inner {
    display: grid;

    gap: 12px;

    border: 1px solid rgba(get-color(on-background-rgb), 0.1);
    border-radius: $input-border-radius;
    padding: 11px 15px;

    background-color: rgba(get-color(on-background-rgb), calc(0.1 - 0.06 * $color-background-brightness / 255));
  }

  &__dropdown-link {
    color: get-color(background);
    white-space: nowrap;
    text-decoration: none;

    &:hover {
      color: get-color(footer-link-hover);
    }
  }

  &__top {
    @include media-query(md) {
      margin-bottom: 115px;
    }
  }

  &__middle {
    @include media-query(md) {
      margin-bottom: 64px;
    }
  }

  &__nav {
    @include media-query(md) {
      max-width: 300px;
      margin-right: -32px;
    }
  }
  &__list {
    &--menu {
      @include media-query(md) {
        margin-left: 0;
      }
    }

    &--bottom {
      @include media-query(md) {
        justify-content: space-between;
      }
    }
  }

  &__menu-link {
    padding: 7px 16px;
  }

  &__powered-by {
    @include media-query(md) {
      margin-right: -16px;
    }

    a {
      color: get-color(background);
    }
  }

  &__copy {
    color: get-color(background);
  }

  &__newsletter-heading {
    font-size: font-size(20px);
    color: get-color(background);
  }

  &__inner {
    padding-top: 57px;
    padding-bottom: 25px;
    border-top: none;
  }

  .newsletter-form {
    max-width: 405px;
    margin: 0 auto;

    @include media-query(md) {
      margin: 0;
    }

    .input-field {
      border: none;
      height: 42px;
      color: get-color(background);
      background: rgba(get-color(background-rgb), 0.1);

      &__input {
        font-size: font-size(14px);
        color: get-color(background);
      }

      ::placeholder {
        color: get-color(background);
      }
    }

    &__input,
    &__button {
      padding: 0;
    }

    &__input-button-container {
      margin: 0;
      align-items: center;
      gap: 10px;
    }
  }

  &__newsletter-header {
    margin-bottom: 24px;
    max-width: 280px;
    margin-left: auto;
    margin-right: auto;

    @include media-query(md) {
      margin-left: 0;
      margin-right: 0;
    }
  }

  &__social-link svg {
    width: 24px;
    height: 24px;
  }
}
