.post-tile {
  $this: &;

  &--large {
    .tile {
      display: block;
      gap: $gutter;

      @include media-query(sm) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: $gutter-sm;
      }

      &__heading {
        font-size: 1.125rem;
        @include media-query(sm) {
          font-size: font-size(52px);
        }
      }

      &__info {
        text-align: center;
        @include media-query(sm) {
          text-align: left;
        }
      }
    }
  }
}